/**
 * 控件类
 */
export class ToolboxItem {
    /**
     * 名称
     */
    name: string;
    /**
     * 类型ID
     */
    clrTypeId: string;
    /**
     * 图片
     */
    imgUrl: string;

    constructor(clrTypeId: string, name?: string, imgUrl?: string) {
        this.clrTypeId = clrTypeId;
        this.name = name;
        this.imgUrl = imgUrl;
    }
}